.accordion__wrapper{color:#fff;padding:0}.accordion__title{text-align:center;margin:2rem 0;font-size:2.5rem}.accordion{width:100%}expended-accordion:first-child{border-top:1px solid rgba(0,0,0,.1)}expended-accordion{display:block;background-color:#fff;color:#1c1c1c;margin:0;border-bottom:1px solid rgba(0,0,0,.1)}.accordion-item-header{padding:.5rem 3rem .5rem 1rem;padding:15px 0;min-height:3.5rem;font-size:14px;font-weight:400;line-height:15.4px;display:flex;align-items:center;position:relative;cursor:pointer;text-transform:uppercase}.accordion-item-header::after{font-size:2rem;position:absolute;right:1rem}.accordion-item-header:hover{font-weight:600}.accordion-item-body{display:grid;grid-template-rows:0fr;overflow:hidden;transition:grid-template-rows .3s ease-in-out}.accordion__button{display:flex;margin:0 auto}.active .accordion-item-body{grid-template-rows:1fr}.accordion-item-body-content{min-height:0;font-size:12px;font-weight:400;line-height:19.2px;border-image:linear-gradient(to right, transparent, #34495e, transparent) 1}.accordion-item-body-content p{margin:0}.accordion-item-body-content div{padding:0 0 10px 0}#loader{display:none;height:50px;margin:0 auto;justify-content:center}.accordion-arrow-icon{margin-left:auto;transition:200ms;transform:rotate(90deg)}.accordion-arrow-icon svg{width:15px;height:15px}.accordion-arrow-icon svg path{stroke-width:1px}expended-accordion.active .accordion-arrow-icon{transform:rotate(0deg)}.accordion-product-metafield{list-style-type:none;padding:0;margin:0}